import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { onGetDashoboardData } from '../../../redux/actions';

import Box from '@material-ui/core/Box';
// import AppAnimate from '../../../@crema/core/AppAnimate';
import GridContainer from '../../../@crema/core/GridContainer';
import Grid from '@material-ui/core/Grid';
import IntlMessages from '../../../@crema/utility/IntlMessages';
import { Fonts } from '../../../shared/constants/AppEnums';
import { AppState } from '../../../redux/store';
// import PinedUsers from './PinedUsers';
import QuickStats from './QuickStats'
import SalesState from './SalesState'
import ReportsTable from './ReportsTable'

const Dashboard = () => {
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(onGetDashoboardData());
    }, [dispatch]);

    const { andUser, winUser, macUser, othUser, reportsOnly, totalClicked, totalVisitor } = useSelector<
        AppState,
        AppState['users']
    >(({ users }) => users);
    console.log(totalClicked, totalVisitor)
    const data = {
        andData: {
            count: andUser || '0'
        },
        winData: {
            count: winUser || '0'
        },
        macData: {
            count: macUser || '0'
        },
        othData: {
            count: othUser || '0'
        }
    }
    const salesState = [
        {
            bgColor: "#49BD65",
            icon: "/assets/images/dashboard/1_monthly_sales.png",
            id: 2,
            type: "Total clicks",
            value: totalClicked || '0',
        },
        {
            bgColor: "#3A3849",
            icon: "/assets/images/dashboard/1_email_sent.png",
            id: 4,
            type: "Total new users",
            value: totalVisitor || '0',
        },
    ];


    return (
        <>
            <Box pt={{ xl: 4 }}>
                <Box
                    component='h2'
                    color='text.primary'
                    mb={{ xs: 4, sm: 4, xl: 6 }}
                    fontSize={16}
                    fontWeight={Fonts.BOLD}>
                    <IntlMessages id='dashboard.metrics' />
                </Box>

                <GridContainer>
                    <Grid item xs={12} md={8}>

                        <Grid item xs={12} md={12}>
                            <QuickStats quickStatsData={data} />
                        </Grid>
                    </Grid>
                    <Grid item xs={12} md={4}>

                        {salesState.map((state, index) => (
                            <Grid item xs={12} sm={12} md={12} key={index} style={{ padding: '3px 0 32px 0' }}>
                                <SalesState state={state} />
                            </Grid>
                        ))}
                    </Grid>
                </GridContainer>
            </Box>
            <Box marginTop={10}>
                <ReportsTable tabledata={reportsOnly} />
            </Box>
        </>
    );
};

export default Dashboard;
