import React from 'react';
import Grid from '@material-ui/core/Grid/index';
import StatsCard from './StatsCard';
import PersonIcon from '@material-ui/icons/Person';
import DescriptionIcon from '@material-ui/icons/Description';
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import ReceiptIcon from '@material-ui/icons/Receipt';
import IntlMessages from '../../../../@crema/utility/IntlMessages';
// import Box from '@material-ui/core/Box';
import { isBreakPointDown } from '../../../../@crema/utility/Utils';
import { blue, indigo, red, teal } from '@material-ui/core/colors';
// import { Fonts } from '../../../shared/constants/AppEnums';
// import makeStyles from '@material-ui/core/styles/makeStyles';
export interface QuickStatsData {
  andData: {
    count: string;
  };
  winData: {
    count: string;
  };
  macData: {
    count: string;
  };
  othData: {
    count: string;
  };
}

// const useStyles = makeStyles(theme => ({
//   textUppercase: {
//     textTransform: 'uppercase',
//   },
// }));

interface QuickStatsProps {
  quickStatsData: QuickStatsData;
}

const QuickStats: React.FC<QuickStatsProps> = ({ quickStatsData }) => {
  // const classes = useStyles();
  return (
    <>
      <Grid container spacing={isBreakPointDown('md') ? 4 : 8}>
        <Grid item xs={12} sm={6}>
          <StatsCard
            icon={
              <PersonIcon
                style={{ fontSize: isBreakPointDown('md') ? 30 : 40 }}
              />
            }
            bgColor={red[500]}
            data={quickStatsData.andData}
            heading={<IntlMessages id='dashboard.totalAndroid' />}
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <StatsCard
            icon={
              <ReceiptIcon
                style={{ fontSize: isBreakPointDown('lg') ? 30 : 40 }}
              />
            }
            bgColor={blue[500]}
            data={quickStatsData.macData}
            heading={<IntlMessages id='dashboard.totalMac' />}
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <StatsCard
            icon={
              <InsertDriveFileIcon
                style={{ fontSize: isBreakPointDown('md') ? 30 : 40 }}
              />
            }
            bgColor={indigo[500]}
            data={quickStatsData.winData}
            heading={<IntlMessages id='dashboard.totalWindows' />}
          />
        </Grid>

        <Grid item xs={12} sm={6}>
          <StatsCard
            icon={
              <DescriptionIcon
                style={{ fontSize: isBreakPointDown('md') ? 30 : 40 }}
              />
            }
            bgColor={teal[500]}
            data={quickStatsData.othData}
            heading={<IntlMessages id='dashboard.totalOthers' />}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default QuickStats;
